    -webkit-transition: position 0.25s cubic-bezier(0.4, 0, 1, 1);
    -moz-transition: position 0.25s cubic-bezier(0.4, 0, 1, 1);
    -ms-transition: position 0.25s cubic-bezier(0.4, 0, 1, 1);
    -o-transition: position 0.25s cubic-bezier(0.4, 0, 1, 1);
    transition: position 0.25s cubic-bezier(0.4, 0, 1, 1);
  }
  .fel.menu.stuck {
    position: fixed;
    top: 14px;
    bottom: 0;
    overflow-y: auto;
  }
  .fel.menu .fel.menu-inner {
    position: static;
    background-color: transparent;
    width: 100%;
  }
  .fel.menu ul.fel li {
    padding-left: 28px;
    border-bottom: 0;
