  .fel.menu ul.fel.sub li .sub li:hover {
    border-left-color: #FD475C;
  }
  .fel.menu ul.fel.sub:last-child {
    border-bottom: 0;
  }
  .fel.menu button.burger-close,
  .fel.menu button.burger {
    display: none;
  }
}
